<template>
  <div>
    <div id="chart_container"></div>
  </div>
</template>

<script>
import { ref, onMounted, onUpdated, onUnmounted, getCurrentInstance, defineProps, defineEmits, watch } from 'vue';

import Plotly from 'plotly.js-dist';

export default {
  props: {
    setting_from: {
      type: Object
    }
  },
  setup(props) {

    const IP = ref(process.env.VUE_APP_BASE_API);
    const instance = getCurrentInstance();///instance.proxy.$Plotly
    const initChart = (from) => {

      var trace1 = {
        x: [0, 1, 2],
        y: ['a', 'b', 'c'],
        mode: 'markers',
        type: 'scatter',
        marker: {
          size: 10,
          opacity: 0.5, // 设置透明度为0.5
        },
      };

      var trace2 = {
        x: [2, 3, 4],
        y: ['d', 'e', 'f'],
        xaxis: 'x2',
        yaxis: 'y2',
        mode: 'markers',
        type: 'scatter',
        marker: {
          size: 10,
          opacity: 0.5, // 设置透明度为0.5
        },
      };

      var trace3 = {
        x: [3, 4, 5],
        y: ['h', 'i', 'j'],
        xaxis: 'x3',
        yaxis: 'y3',
        mode: 'markers',
        type: 'scatter',
        marker: {
          size: 10,
          opacity: 0.5, // 设置透明度为0.5
        },
      };

      var data = [trace1, trace2, trace3];

      var layout = {
        width: 700,
        height: 600,
        grid: {
          rows: 3,
          columns: 1,
          pattern: 'independent',
          roworder: 'bottom to top'
        }
      };

      // Plotly.newPlot('myDiv', data, layout);
      instance.proxy.$Plotly.newPlot('chart_container', props.setting_from.data, props.setting_from.layout,{ displayModeBar: false });
    };

    //计算属性，监听propsz值的改变
    watch(props, (nweProps, oldProps) => {
      console.log(nweProps);
      // console.log(oldProps);

      initChart(nweProps.setting_from);
    })

    onMounted(() => {
      initChart(props.setting_from);
      console.log('传参');
      console.log(props.setting_from);
    });

    onUpdated(() => {

    });

    onUnmounted(() => {

    });

    return {
      props
    };
  }
}

</script>
