<template>
  <div style="background-color: #fff;padding: 10px;">
    <div class="table_header">
      <el-input v-model="searchKeyword" placeholder="请输入文字进行搜索" clearable style="width: 300px;margin-bottom: 15px;">
        <template #append>
          <el-button :icon="'Search'" @click="handleSearch" />
        </template>
      </el-input>
      <div>{{ this.from_data.file_name }}</div>
    </div>

    <el-table :data="table_Data" border style="width: 100%" height="700" v-loading="is_loading">
      <el-table-column v-for="(item, index) in columns" :key="index" :label="item" sortable width="300">
        <template v-slot:default="scope">
          <div>
            {{ scope.row[index] }}
          </div>
        </template>
      </el-table-column>
    </el-table>
    <div style="width: 100;background-color: #fff;">
      <el-pagination layout="prev, pager, next" :total="table_params.total" :page-size="table_params.pageSize"
        :current-page="table_params.currentPage" @current-change="handleCurrentChange" />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    from_data: {
      type: Object
    },
  },
  data() {
    return {
      table_params: {
        pageSize: 10, // 每页显示的条数
        currentPage: 1, // 当前页码
        total: 1,
      },
      raw_table_data: null,
      filter_raw_table_data: null,//过滤的全部数据
      table_Data: null,
      columns: null,

      searchKeyword: '',//搜索文字
    };
  },

  components: {},

  computed: {

  },

  mounted() {

    console.log('测试是否加载的');
    console.log(this.from_data);
    this.init();
  },

  methods: {
    init() {
      this.raw_table_data = this.from_data.data;
      this.columns = this.from_data.columns;

      if (!this.searchKeyword) {
        this.table_Data = this.from_data.data.slice(0, 10);
        this.table_params.total = this.from_data.data.length;
        this.filter_raw_table_data = this.from_data.data;
      } else {
        const keyword = this.searchKeyword.toLowerCase();
        let list = this.raw_table_data.filter((subArray) => {
          // 遍历子数组的每个元素来查找匹配项
          for (const element of subArray) {
            if (String(element).toLowerCase().includes(keyword)) {
              return true; // 如果找到匹配项，返回 true，保留该子数组
            }
          }
          return false; // 没有找到匹配项，过滤掉该子数组
        });

        this.filter_raw_table_data = list;
        this.table_Data = list.slice(0, 10);
        this.table_params.total = list.length;
      }
    },
    //分页
    handleCurrentChange(val) {
      this.table_params.currentPage = val;
      const start = (val - 1) * this.table_params.pageSize;
      const end = start + this.table_params.pageSize;
      this.table_Data = this.filter_raw_table_data.slice(start, end);
    },
    //搜索
    handleSearch() {
      console.log('输入');
      this.table_params.currentPage = 1;
      this.init();
    },

  }
}

</script>
