<!--  -->
<template>
  <div v-loading="is_loading">
    <div v-if="table_all_list.length > 0">
      <div v-for="(item, index) in table_all_list" :key="index" style="margin-bottom: 15px;">
        <!-- {{ item }} -->
        <TbaleUnit :from_data="item"></TbaleUnit>
      </div>
    </div>
    <el-empty v-else description="暂无数据" />
  </div>
</template>

<script>
import { ref, onBeforeMount, onMounted, onUpdated, onUnmounted, getCurrentInstance, watch, defineEmits } from 'vue';
import axios from "axios";
import TbaleUnit from './module/table.vue';
export default {
  props: {
    other_value: {
      type: Object
    },
    data: {
      type: Object
    },
    API_ROOT: {
      type: String
    },
    ticket: {
      type: String
    }
  },
  components: {
    TbaleUnit
  },
  setup(props, { emit }) {

    const is_loading = ref(false);
    const table_data = ref(null);
    const show_table_data = ref(null);

    const columns = ref(null);

    const table_all_list = ref([]);


    const params = ref({
      pageSize: 10, // 每页显示的条数
      currentPage: 1 // 当前页码
    });

    watch(props, (nweProps, oldProps) => {
      init(nweProps.other_value);
    })

    const init = (from = null) => {
      const { callback_function, callback_name, callback_route } = props.data;
      let data = {
        callback_function, callback_name
      };
      if (from) data = { ...data, ...from };
      is_loading.value = true;
      axios({
        headers: {
          ticket: props.ticket
        },
        method: "POST",
        url: props.API_ROOT + callback_route,
        data: data,
      }).then((result) => {
        // let obj = {
        //   columns: Object.keys(result.data.data[0]),
        //   data: result.data.data
        // };
        const item = result.data.data;
        table_all_list.value = [item];
        is_loading.value = false;
      }).catch(error => {
        is_loading.value = false;
      });
    };

    onBeforeMount(() => {

    });
    onMounted(() => {
      if (props.other_value) {
        init(props.other_value);
      }
    });
    onUpdated(() => {

    });
    onUnmounted(() => {

    });
    return {
      props, table_data, show_table_data, params, columns, table_all_list,
    };
  }
}
</script>
<style scoped>
.from_unit {
  margin-bottom: 30px;
  text-align: right;
}
</style>