<template>
  <div v-loading="is_loading">
    <div v-if="table_list">
      <Table v-for="(item, index) in table_list" :key="index" :from_data="item" style="margin-bottom: 15px;"></Table>
    </div>
    <el-empty v-else description="暂无数据" />
  </div>
</template>

<script>
import Table from './table.vue';
import axios from "axios";
export default {
  props: {
    other_value: {
      type: Object
    },
    from_data: {
      type: Object
    },
    API_ROOT: {
      type: String
    },
    ticket: {
      type: String
    }
  },
  data() {
    return {
      table_list: null,
      is_loading: false,
    };
  },

  watch: {
    other_value: {
      deep: true,
      handler(newData, oldDate) {
        this.init(newData);
      }
    }
  },

  components: { Table },

  computed: {

  },

  mounted() {
    console.log('参数数据');
    console.log(this.from_data);

    if (this.other_value) {
      this.init(this.other_value);
    }
  },

  methods: {
    init(from) {
      const key = this.from_data.data_path;
      let data = {
        "plot_type": 1,
      };
      if (from) {
        data = { ...data, ...from }
      }
      this.is_loading = true;
      axios({
        headers: {
          ticket: this.ticket
        },
        method: "POST",
        url: this.API_ROOT + this.from_data.api_url,
        data: data,
      }).then((res) => {
        console.log('Tables 接口数据结果');
        console.log(res);
        if (res.data.image_task_id) {
          localStorage.setItem('image_task_id', res.data.image_task_id || null);
        }
        const tableData = res.data[key];
        if (Array.isArray(tableData)) {//列表
          this.table_list = tableData || null;
        } else {
          this.table_list = [tableData] || null;
        }
        this.is_loading = false;
      }).catch(error => {
        this.is_loading = false;
      });
    },

  }
}

</script>
<style scoped></style>