

<template>
  <div class="parameter_container">
    <!--  <div class="right_header">
      <h4>{{ props.data.title }}</h4>
      <el-button type="text" title="收起">
        <component is="Expand" class="my-component"></component>
      </el-button>
    </div> -->
    <div style="max-height: 840px;overflow-y: auto;">
      <el-collapse>
        <el-collapse-item v-for="(item, key, index) in props.form_data" :key="index" :title="item.show_name" :name="index"
          v-show="item.is_show">
          <!-- 自定义文本单独做处理 -->
          <div class="length_width_spacing_box" v-if="item.type == 'custom_text'">
            <div v-for="(e, i) in item.data" :key="i" :style="{ width: e.width }">
              <div v-if="e.form_type == 'text_button'" class="color_container">
                <!-- <el-button draggable="true" style="width: 45%;">{{ e.value }}</el-button> -->
                <el-input draggable="true" style="width: 100%;" v-model="e.value" :placeholder="e.placeholder"
                  id="draggable_box" @dragstart="drag_start" @dragend="drag_end">
                  <template #prefix>
                    <span>{{ e.title }}</span>
                  </template>
                </el-input>
              </div>
              <div v-if="e.form_type == 'family_select'" class="color_container">
                <el-select v-model="e.value" :placeholder="e.placeholder" size="large" title="选择字体" style="width: 100%;">
                  <el-option v-for="u in e.select_list" :key="u.value" :label="u.label" :value="u.value" />
                </el-select>
              </div>
              <div v-if="e.form_type == 'input'" class="color_container">
                <el-input v-model="e.value" :type="e.type" :placeholder="e.placeholder">
                  <template #prefix>
                    <span>{{ e.title }}</span>
                  </template>
                </el-input>
              </div>
              <div v-if="e.form_type == 'colorpicker'" class="color_container">
                <el-input v-model="e.value" style="width: calc(100% - 50px);" :placeholder="e.placeholder">
                  <template #prefix>
                    <span>{{ e.title }}</span>
                  </template>
                </el-input>
                <div style="width: 50px;display: inline-block;text-align: right;vertical-align: middle;">
                  <el-color-picker v-model="e.value" />
                </div>
              </div>
            </div>
          </div>
          <div class="length_width_spacing_box" v-else>
            <div v-for="(e, k, i) in  item.data" :key="i"
              :style="{ display: e.is_show ? 'inline-block' : 'none', width: e.width, marginBottom: '10px' }"
              :title="e.note">
              <!-- 带标题的输入框 -->
              <el-input v-model="e.value" placeholder="请输入" v-if="e.title && e.form_type == 'input'" :type="e.type"
                @keyup.enter="handleEnter" @change="change_form_data">
                <template #prefix>
                  <span>{{ e.title }}</span>
                </template>
              </el-input>
              <el-input v-else-if="e.form_type == 'input'" :type="e.type" v-model="e.value" :placeholder="e.placeholder"
                @change="change_form_data" />
              <!-- 单个 勾选框 -->
              <el-checkbox v-else-if="e.form_type == 'checkbox_radio'" v-model="e.value" :label="e.show_name" size="large"
                @change="change_form_data" />
              <!-- 下拉框 -->
              <el-select v-else-if="e.form_type == 'select'" v-model="e.value" :placeholder="e.placeholder" size="large"
                title="选择字体" style="width: 100%;" @change="(val) => { selectChang(val, e, key) }">
                <el-option v-for="u in e.select_list" :key="u.value" :label="u.label" :value="u.value" />
              </el-select>
              <!-- 颜色盘 -->
              <div v-else-if="e.form_type == 'colorpicker'">
                <div style="width: 50px;display: inline-block;">
                  <el-color-picker v-model="e.value" @change="change_form_data" />
                </div>
                <el-input v-model="e.value" style="width: calc(100% - 50px);" :placeholder="e.placeholder"
                  @change="change_form_data" />
              </div>
              <!-- 带切换栏 3层 -->
              <div v-else-if="e.form_type == 'tabs'" style="text-align: center;">
                <el-radio-group v-model="item.tab_value">
                  <el-radio-button v-for="(c, v) in e.select_list" :key="v" :label="c.value">{{ c.label
                  }}</el-radio-button>
                </el-radio-group>
              </div>
              <!-- tab切换的内容(坐标轴) -->
              <div v-else-if="e.form_type == 'tabs_unit' && e.tab_value == item.tab_value">
                <div v-for="(h, j) in e.data" :key="j" style="display: inline-block;width: 50%;">
                  <el-checkbox v-if="h.form_type == 'checkbox_radio'" v-model="h.value" :label="h.show_name" size="mini"
                    style="width: 100%;" @change="change_form_data" />
                  <el-input v-if="h.form_type == 'input'" v-model="h.value" :placeholder="h.placeholder"
                    @change="change_form_data" />
                </div>
              </div>
              <!-- tab切换的内容(颜色设置)  && e.tab_value == item.tab_value -->
              <div v-else-if="e.form_type == 'tabs_color_list'" style="height: 260px;">
                <!--  {{ e.value }} -->

                <div v-show="item.tab_value == 1">
                  <ColorSelect :color_list="e.select_list" :select_color="color_select" :ticket="props.ticket"
                    :API_ROOT="props.API_ROOT" :from_data="item" @updataColor="(val) => { updataColor(val, key, k) }">
                  </ColorSelect>
                </div>
                <div v-show="item.tab_value == 2">
                  <el-input v-model="e.value" :rows="4" type="textarea" placeholder="Please input"
                    @change="change_form_data" />
                </div>
              </div>
              <!--  <div v-else-if="e.form_type == 'tabs_custom_colors' && e.tab_value == item.tab_value">
                <el-input v-model="e.value" :rows="4" type="textarea" placeholder="Please input" />
              </div> -->

              <!-- icon按钮 -->
              <el-button v-else-if="e.form_type == 'button'">
                <component is="Link" class="my-component"></component>
              </el-button>
            </div>
          </div>
        </el-collapse-item>
        <el-collapse-item title="下载" v-if="props.data.handle_function == 1">
          <el-button type="primary" @click="download_img">保存为图片</el-button>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>

<script>
import { ref, onBeforeMount, onMounted, onUpdated, onUnmounted, getCurrentInstance, watch, defineEmits } from 'vue';
import ColorSelect from '../ColorSelect/index.vue';
import * as d3 from 'd3';
export default {
  props: {
    title: {
      type: String
    },
    data: {
      type: Object
    },
    form_data: {
      type: Object
    },
    is_default_value: {
      type: Boolean
    },
    ticket: {
      type: String
    },
    API_ROOT: {
      type: String
    }
  },
  components: {
    ColorSelect
  },
  setup(props, { emit }) {
    const color_select = ref(null);//选择的颜色

    const template_format_data = ref(null);//动态格式化参数

    //静态图参数
    const trends_handle = (data) => {
      let from = {};
      for (const key in data) {
        for (const e in data[key].data) {
          from[e] = data[key].data[e].value;
        }
      }
      return from;
    };
    // 动态参数
    const format_data = (data) => {
      let from = {};
      for (const key in data) {
        from[key] = {};
        for (const e in data[key].data) {
          if (e == 'xaxis' || e == 'yaxis') {
            from[key][e] = {};
            for (const o in data[key].data[e].data) {
              if (data[key].data[e].data[o].value) from[key][e][o] = data[key].data[e].data[o].value;
            }
          } else {
            if (data[key].data[e].value) from[key][e] = data[key].data[e].value;
          }
        }
      }
      return from;
    };


    const updataColor = (val, key, k) => {
      color_select.value = val
      props.form_data[key].data[k].value = val;
      setTimeout(() => {//更新
        // emit('updataFormdata', format_data(props.form_data));
        if (props.data.handle_function == 1) {
          emit('updataFormdata', trends_handle(props.form_data));
        }
        if (props.data.handle_function == 2) {
          emit('updataFormdata', format_data(props.form_data));
        }
      }, 300);
    };

    //选择框监听
    const selectChang = (val, e, k) => {
      if (k == 'legend') {//图例方位
        props.form_data[k].data.x['value'] = val[0];
        props.form_data[k].data.y['value'] = val[1];
      }
    };
    //输入框回车
    const handleEnter = () => {
      if (!props.data.is_input_monitoring) {
        if (props.data.handle_function == 1) {
          emit('updataFormdata', trends_handle(props.form_data));
        }
      }
    };




    //文本拖拽-开始
    const drag_start = (event) => {
      template_format_data.value = format_data(props.form_data);
    };
    //文本拖拽-结束
    const drag_end = (event) => {
      const svgContainer = d3.select(`#${props.data.chart_id} svg`);
      const svgRect = svgContainer.node().getBoundingClientRect();//svg的位置
      /* 鼠标相对于svg的偏移量 */
      const offsetX = event.x - svgRect.left;
      const offsetY = event.y - svgRect.top;
      /* 文字样式 */
      const { text, font_family, font_size, font_color, background_color } = template_format_data.value.custom_text;
      // 添加一个组，用于包裹文本和背景矩形
      const textGroup = svgContainer.append('g')
        .attr('id', 'text-group');
      // 添加文本元素
      const textBox = textGroup.append('text')
        .attr('x', offsetX)
        .attr('y', offsetY)
        .attr("fill", font_color)
        .style('font-size', `${font_size}px`)
        .style('font-family', font_family)
        .text(text);
      // 获取文本元素的边界框信息
      const textBBox = textBox.node().getBBox();
      // 添加背景矩形
      textGroup.insert('rect', 'text')
        .attr('x', textBBox.x - 5) // 给矩形增加一些 padding
        .attr('y', textBBox.y - 5) // 给矩形增加一些 padding
        .attr('width', textBBox.width + 10) // 给矩形增加一些 padding
        .attr('height', textBBox.height + 10) // 给矩形增加一些 padding
        .attr('fill', background_color); // 设置背景色为黄色
    };

    const change_form_data = (val) => {
      console.log('监听数据');
      console.log(val);
      console.log(props.form_data);
      if (props.data.handle_function == 1) {
        emit('updataFormdata', trends_handle(props.form_data));
      }
      if (props.data.handle_function == 2) {
        emit('updataFormdata', format_data(props.form_data));
      }
    };

    // 将 Base64 数据转换为 Blob 对象的函数
    function base64ToBlob(base64) {
      const parts = base64.split(';base64,');
      const contentType = parts[0].split(':')[1];
      const byteCharacters = atob(parts[1]);
      const byteNumbers = new Array(byteCharacters.length);
      for (let i = 0; i < byteCharacters.length; i++) {
        byteNumbers[i] = byteCharacters.charCodeAt(i);
      }
      const byteArray = new Uint8Array(byteNumbers);
      return new Blob([byteArray], { type: contentType });
    };


    const download_img = () => {
      if (props.data.handle_function == 1) {
        let img = document.getElementsByClassName('el-image__inner');
        console.log(img[0].currentSrc);
        const base64Data = img[0].currentSrc; // 替换为实际的 Base64 数据
        // 创建一个 Blob 对象，将 Base64 数据作为 Blob 的数据
        const blob = base64ToBlob(base64Data);
        // 创建一个临时链接，用于下载
        const url = URL.createObjectURL(blob);
        // 创建一个隐藏的 <a> 元素并设置下载属性
        const link = document.createElement('a');
        link.href = url;
        link.download = 'saved_image.png'; // 指定文件名
        // 触发点击下载
        link.click();
        // 释放临时链接的资源
        URL.revokeObjectURL(url);

      } else {
        console.log('点击下载');
        console.log(props.data);
      }
    };

    onBeforeMount(() => {

    });
    onMounted(() => {

    });
    onUpdated(() => {

    });
    onUnmounted(() => {

    });
    return {
      props, color_select, template_format_data, updataColor, selectChang, handleEnter, drag_start, drag_end, change_form_data, download_img
    };
  }
}
</script>
