

<template>
  <div style="width: 100%; height: 100%">
    <el-collapse v-model="activeNames">
      <el-collapse-item title="基础参数" :name="1">
        <el-form v-if="is_show_from" label-width="120px" ref="left_from">
          <div v-for="(i, index) in form_data" :key="index">
            <el-form-item v-if="i.is_show && !i.is_advanced" :label="i.show_name" :rules="{ required: i.required }">
              <template #label>
                <span class="labelcustom" :title="i.show_name">{{ i.show_name }}</span>
                <el-popover placement="right" :width="300" effect="dark" trigger="hover">
                  <p v-html="i.show_name + '：<br>' + i.note"></p>
                  <template #reference>
                    <InfoFilled style="margin:6px 0 0 3px;cursor: pointer;width: 1.2em; height: 1.2em;color:#B6B6B6">
                    </InfoFilled>
                  </template>
                </el-popover>
              </template>
              <el-input @change="updateInputData" v-if="i.form_type == 'input'" :type="i.type" v-model="i.value"
                :placeholder="i.placeholder" />
              <el-radio-group v-else-if="i.form_type == 'radio'" @change="updateInputData" v-model="i.value">
                <el-radio style="width: 100px" v-for="(r_i, r_index) in i.data" :key='r_index' :label="r_index"
                  :title="r_i"><span class="labelcustom">{{ r_i }}</span></el-radio>
              </el-radio-group>
              <el-checkbox-group v-else-if="i.form_type == 'checkbox'" @change="updateInputData" v-model="i.value">
                <el-checkbox style="width: 100px" v-for="(c_i, c_index) in i.data" :key='c_index' :label="c_index"
                  :title="c_i"><span class="labelcustom">{{ c_i }}</span></el-checkbox>
              </el-checkbox-group>
              <el-select v-else-if="i.form_type == 'select'" @change="updateInputData" style="width:100%"
                v-model="i.value" clearable :placeholder="i.placeholder">
                <el-option v-for="(s_i, s_index) in  i.data" :key="s_index" :label="s_i" :value="s_index" />
              </el-select>
              <el-switch v-else-if="i.form_type == 'switch'" @change="updateInputData" v-model="i.value" />
              <el-cascader style="width:100%" @change="updateInputData" v-else-if="i.form_type == 'cascader'"
                v-model="i.value" clearable :options="i.data" :placeholder="i.placeholder" />
              <div v-else-if="i.form_type == 'slider'" class="slider-demo-block">
                <el-slider @change="updateInputData" :range="typeof i.value == 'object' ? true : false" v-model="i.value"
                  :max="i.data && i.data.max ? i.data.max : 100" :min="i.data && i.data.min ? i.data.min : 0"
                  show-input />
              </div>
              <div v-else-if="i.form_type == 'upload'" style="width:100%">
                <div class="aboutStyle">
                  <el-input readonly v-model="i.value" placeholder="请选择文件进行上传" />
                </div>
                <el-popover placement="right" :width="300" effect="dark" trigger="hover">
                  <div>
                    <p>文件信息</p>
                    <p v-for="(f_i, f_index) in i.fileList" :key="f_index">{{ f_i.name }} </p>
                  </div>
                  <template #reference>
                    <el-button style="float:right;margin-left:10px" title="上传到服务器" @click="submitUpload(index)"><el-icon>
                        <List />
                      </el-icon></el-button>
                  </template>
                </el-popover>
                <el-upload style="float:right" :id="index" :ref="index" :action="i.action"
                  :name='i.upload_name ? i.upload_name : "file"' :accept="i.accept" :file-list="i.fileList"
                  :limit="i.limit ? i.limit : 0" :on-change="(file, files) => handleChange(file, files, index)"
                  :on-remove="(file, files) => handleRemove(file, files, index)" :auto-upload="false"
                  :show-file-list="false" :http-request="(file) => uploadFile(file, index)"
                  :multiple="i.type == 'multiple' || i.type == 'folder' ? true : false">
                  <el-popover v-if="i.accept" placement="right" :width="300" effect="dark" trigger="hover">
                    <p>仅支持上传{{ i.accept }}</p>
                    <template #reference>
                      <el-button v-loading="i.loading">选取文件</el-button>
                    </template>
                  </el-popover>
                  <el-button v-loading="i.loading" v-else>选取文件</el-button>
                </el-upload>
              </div>
            </el-form-item>
          </div>
        </el-form>
      </el-collapse-item>
      <el-collapse-item title="高级参数" :name="2" v-if="is_advanced">
        <el-form v-if="is_show_from" label-width="120px" ref="left_from">
          <div v-for="(i, index) in form_data" :key="index">
            <el-form-item v-if="i.is_show && i.is_advanced" :label="i.show_name" :rules="{ required: i.required }">
              <template #label>
                <span class="labelcustom" :title="i.show_name">{{ i.show_name }}</span>
                <el-popover placement="right" :width="300" effect="dark" trigger="hover">
                  <p v-html="i.show_name + '：<br>' + i.note"></p>
                  <template #reference>
                    <InfoFilled style="margin:6px 0 0 3px;cursor: pointer;width: 1.2em; height: 1.2em;color:#B6B6B6">
                    </InfoFilled>
                  </template>
                </el-popover>
              </template>
              <el-input @change="updateInputData" v-if="i.form_type == 'input'" :type="i.type" v-model="i.value"
                :placeholder="i.placeholder" />
              <el-radio-group v-else-if="i.form_type == 'radio'" @change="updateInputData" v-model="i.value">
                <el-radio style="width: 100px" v-for="(r_i, r_index) in i.data" :key='r_index' :label="r_index"
                  :title="r_i"><span class="labelcustom">{{ r_i }}</span></el-radio>
              </el-radio-group>
              <el-checkbox-group v-else-if="i.form_type == 'checkbox'" @change="updateInputData" v-model="i.value">
                <el-checkbox style="width: 100px" v-for="(c_i, c_index) in i.data" :key='c_index' :label="c_index"
                  :title="c_i"><span class="labelcustom">{{ c_i }}</span></el-checkbox>
              </el-checkbox-group>
              <el-select v-else-if="i.form_type == 'select'" @change="updateInputData" style="width:100%"
                v-model="i.value" clearable :placeholder="i.placeholder">
                <el-option v-for="(s_i, s_index) in  i.data" :key="s_index" :label="s_i" :value="s_index" />
              </el-select>
              <el-switch v-else-if="i.form_type == 'switch'" @change="updateInputData" v-model="i.value" />
              <el-cascader style="width:100%" @change="updateInputData" v-else-if="i.form_type == 'cascader'"
                v-model="i.value" clearable :options="i.data" :placeholder="i.placeholder" />
              <div v-else-if="i.form_type == 'slider'" class="slider-demo-block">
                <el-slider @change="updateInputData" :range="typeof i.value == 'object' ? true : false" v-model="i.value"
                  :max="i.data && i.data.max ? i.data.max : 100" :min="i.data && i.data.min ? i.data.min : 0"
                  show-input />
              </div>
              <div v-else-if="i.form_type == 'upload'" style="width:100%">
                <div class="aboutStyle">
                  <el-input readonly v-model="i.value" placeholder="请选择文件进行上传" />
                </div>
                <el-popover placement="right" :width="300" effect="dark" trigger="hover">
                  <div>
                    <p>文件信息</p>
                    <p v-for="(f_i, f_index) in i.fileList" :key="f_index">{{ f_i.name }} </p>
                  </div>
                  <template #reference>
                    <el-button style="float:right;margin-left:10px" title="上传到服务器" @click="submitUpload(index)"><el-icon>
                        <List />
                      </el-icon></el-button>
                  </template>
                </el-popover>
                <el-upload style="float:right" :id="index" :ref="index" :action="i.action"
                  :name='i.upload_name ? i.upload_name : "file"' :accept="i.accept" :file-list="i.fileList"
                  :limit="i.limit ? i.limit : 0" :on-change="(file, files) => handleChange(file, files, index)"
                  :on-remove="(file, files) => handleRemove(file, files, index)" :auto-upload="false"
                  :show-file-list="false" :http-request="(file) => uploadFile(file, index)"
                  :multiple="i.type == 'multiple' || i.type == 'folder' ? true : false">
                  <el-popover v-if="i.accept" placement="right" :width="300" effect="dark" trigger="hover">
                    <p>仅支持上传{{ i.accept }}</p>
                    <template #reference>
                      <el-button v-loading="i.loading">选取文件</el-button>
                    </template>
                  </el-popover>
                  <el-button v-loading="i.loading" v-else>选取文件</el-button>
                </el-upload>
              </div>
            </el-form-item>
          </div>
        </el-form>
      </el-collapse-item>

    </el-collapse>


  </div>
</template>
<script >
import { ref } from 'vue'
import axios from "axios";
export default {
  props: ["API_ROOT", 'input_data', "is_default_value", "ticket", "task_id", 'app_id'],
  data() {
    return {
      form_data: {},
      is_show_from: false,
      callback_data: new FormData(),
      activeNames: [1],

      basic_parameters: {},//基础参数
      advanced_parameters: {},//高级参数

      is_advanced: false,//是否存在高级参数
    };
  },
  created() {
    this.is_show_from = true
  },
  mounted() {
    this.init()
    for (let i in this.form_data) {
      if (this.form_data[i].form_type == 'upload' && this.form_data[i].type == 'folder') {
        setTimeout(this.switchChange(i), 1000);
      }
    }
  },
  methods: {
    /*  init() {//获取表单配置
       if (this.input_data) {
         this.form_data = this.input_data
         this.input_data_update()
       } else {
         // axios({
         //   headers: {
         //     ticket: this.ticket
         //   },
         //   method: "POST",
         //   url: this.API_ROOT + "/ms_jk/get_template_config",
         //   data: {
         //     ct: 1
         //   },
         // }).then((res) => {
         //   console.log('左侧参数lo');
         //   console.log(res);
 
         //   for (const key in res.data) {//有高级参数则展示高级参数/没有则隐藏
         //     if (res.data[key].is_advanced) this.is_advanced = true;
         //   }
 
         //   this.form_data = res.data;
         //   this.input_data_update();
         //   this.get_backfill_data();
         // });
 
 
 
         for (const key in this.template_data) {//有高级参数则展示高级参数/没有则隐藏
           if (this.template_data[key].is_advanced) this.is_advanced = true;
         }
         this.form_data = this.template_data;
         this.input_data_update();
         this.get_backfill_data();
       }
     }, */
    init() {//获取表单配置
      if (this.input_data) {
        this.form_data = this.input_data;
        for (const key in this.input_data) {//有高级参数则展示高级参数/没有则隐藏
          if (this.input_data[key].is_advanced) this.is_advanced = true;
        }
        this.input_data_update();
        this.get_backfill_data();
      }
    },
    //获取回填数据
    get_backfill_data() {
      if (!this.task_id) return false;//可能是第一次，没有任务记录
      axios({
        headers: {
          ticket: this.ticket
        },
        method: "POST",
        url: this.API_ROOT + "/ms_jk/get_params",
        data: {
          task_id: this.task_id
        },
      }).then((res) => {
        console.log('回填参数');
        console.log(res);
        if (res.status == 200) {
          let params = res.data.inputs_json;
          params['task_name'] = res.data.name;
          this.$emit('updataParams', params);
          for (const key in params) {
            this.form_data[key].value = params[key];
          }
        }
        console.log(res);
      });
    },


    input_data_update() {
      console.log(this.is_default_value)
      for (let i in this.form_data) {
        if (this.is_default_value) {
          this.form_data[i].value = this.form_data[i].default_value
        } else {
          this.form_data[i].value =
            (['input', 'radio', 'select', 'cascader', 'upload'].indexOf(this.form_data[i].form_type) > -1 ||
              (this.form_data[i].form_type == 'slider' && typeof this.form_data[i].default_value != 'object')) ?
              null :
              ((this.form_data[i].form_type == 'switch') ? false :
                (((this.form_data[i].form_type == 'slider' && typeof this.form_data[i].default_value == 'object')) ? [0, 0] : []))

        }
        if (this.form_data[i].form_type == 'slider' && typeof this.form_data[i].value == 'object') {
          this.form_data[i].value = ref(this.form_data[i].value)
        }
        if (!this.form_data[i].condition) {
          this.form_data[i].condition = []
        }
        this.form_data[i].is_show = this.is_show_fun(this.form_data[i].condition);
        if (this.form_data[i].form_type == 'upload') {
          this.form_data[i].fileList = []
          this.form_data[i].folder_data = new FormData();
          this.form_data[i].loading = false
        }
      }
    },
    is_show_fun(condition) {
      let is_show = true
      for (let i in condition) {
        if (condition[i].field && this.form_data[condition[i].field] && is_show) {
          if (!eval("this.form_data[condition[i].field].value" + condition[i].way + '"' + condition[i].value + '"')) {
            is_show = false
          }
        }
      }
      return is_show
    },
    handleChange(file, fileList, index) {
      let existFile = fileList
        .slice(0, fileList.length - 1)
        .find((f) => f.name === file.name);
      if (existFile) {
        this.$message.info("当前文件已经存在!");
        fileList.pop();
      }
      this.form_data[index].fileList = fileList

    },
    handleRemove(file, fileList, index) {
      this.form_data[index].fileList = fileList
    },
    uploadFile(file, index) {
      let name = this.form_data[index].upload_name || 'file';
      if (this.form_data[index].type) {
        this.form_data[index].folder_data.append(name + '[]', file.file);
      } else if (this.form_data[index].fileList.length) {
        this.form_data[index].folder_data.append(name, file.file);
      }
    },
    switchChange(ref_name) {
      const upload_box = document.getElementById(ref_name);
      const input = upload_box.querySelector('input');
      input.setAttribute('webkitdirectory', true);
    },
    submitUpload(index) {
      if (!this.form_data[index].fileList || !this.form_data[index].fileList.length) {
        this.$message.error("数据不存在，请选择数据");
        return
      }
      this.folder_data = new FormData(); // new formData对象
      let name = this.form_data[index].upload_name || 'file';
      if (this.form_data[index].type) {
        for (let file of this.form_data[index].fileList) {
          console.log(file)
          this.form_data[index].folder_data.append(name + '[]', file.raw);
        }
      } else if (this.form_data[index].fileList.length) {
        this.form_data[index].folder_data.append(name, this.form_data[index].fileList[0].raw);
      } else {
        this.$message.error("数据有误，请重新选择");
        return
      }
      axios({
        // headers: this.headers,
        method: "POST",
        url: this.API_ROOT + this.form_data[index].action,
        data: this.form_data[index].folder_data
      }).then((res) => {
        if (res.data[this.form_data[index].data_path]) {
          this.form_data[index].value = res.data[this.form_data[index].data_path]
          this.updateInputData()
          if (this.form_data[index].callback_route) {
            this.callback_get_data(index);
          }
        } else {
          this.$message.error("上传失败，请重新上传");
        }
        this.form_data[index].fileList = []
        this.form_data[index].folder_data = new FormData();
        this.form_data[index].loading = false
      });
    },
    updateInputData() {
      let data = {};
      for (let i in this.form_data) {
        data[i] = this.form_data[i].value
        this.form_data[i].is_show = this.is_show_fun(this.form_data[i].condition);
      }
      this.$emit('updateInputData', data);
    },
    callback_get_data(index) {
      console.log('这个index是什么');
      console.log(index);
      console.log(this.form_data[index].callback_name);
      let data = {
        callback_function: this.form_data[index].callback_function
      }
      data['callback_name'] = this.form_data[index].callback_name;
      data[this.form_data[index].callback_name] = this.form_data[index].value
      this.callback_data = new FormData()
      axios({
        // headers: this.headers,
        method: "POST",
        url: this.API_ROOT + this.form_data[index].callback_route,
        data: data
      }).then((res) => {
        if (this.form_data[index].callback_data_path) {
          this.form_data[index].callback_data = res.data[this.form_data[index].callback_data_path]
        } else {
          this.form_data[index].callback_data = res.data
        }
        for (let i in this.form_data) {
          if (this.form_data[i].callback_field_data && this.form_data[i].callback_field_data == index) {
            this.form_data[i].data = this.form_data[index].callback_data
          }
        }
        console.log(this.form_data)
      });
    },

    //获取示例数据 
    getSampleData() {
      axios({
        headers: {
          ticket: this.ticket
        },
        method: "POST",
        url: this.API_ROOT + "/ms_jk/sample",
        data: {
          app_id: this.app_id
        }
      }).then((res) => {
        console.log('获取示例数据');
        console.log(res);
        let sample = res.data.data;
        for (const key in sample) {
          console.log(key);
          this.form_data[key].value = sample[key];
        }
      });
    },

  }
};
</script>
<style scoped>
