  max-width: 100px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  padding: 0px;
  position: relative;
}

.el-radio {
  white-space: inherit !important;
}

.el-checkbox {
  white-space: inherit !important;
}

.aboutStyle {
  float: left;
  width: calc(100% - 158px) !important;
}

.slider-demo-block {
  display: flex;
  width: 100%;
  align-items: center;
}

.slider-demo-block .el-slider {
  margin-top: 0;
  margin-left: 12px;
}
</style>
