

<template>
  <div>
    <div style="margin-bottom: 10px;">
      <el-select v-model="style_value" placeholder="Select" size="default" style="width: 100%;" @change="selectStyle">
        <el-option v-for="item in style_list" :key="item" :label="item" :value="item" />
      </el-select>
    </div>
    <div>
      <div class="data_nature_box">
        <div>数据的性质</div>
        <div>
          <el-popover placement="top-start" title="说明" :width="300" trigger="hover">
            <template #reference>
              <el-icon>
                <InfoFilled />
              </el-icon>
            </template>
            <div v-for="(item, index) in data_natures" :key="index">
              {{ item.remark }}
            </div>
          </el-popover>
        </div>
      </div>
      <div>
        <el-radio-group v-model="color_type" @change="colorTypeChange">
          <el-radio v-for="(item, index) in data_natures" :key="index" :label="item.value" size="large">{{ item.label
          }}</el-radio>
        </el-radio-group>

        <!-- <el-checkbox v-model="color_type1" label="sequential" size="large" @change="colorTypeChange">顺序</el-checkbox>
        <el-checkbox v-model="color_type1" label="diverging" size="large" @change="colorTypeChange">发散</el-checkbox>
        <el-checkbox v-model="color_type1" label="qualitative" size="large" @change="colorTypeChange">定性</el-checkbox> -->
      </div>
    </div>
    <div class="dropdown" ref="dropdown" @click="toggleDropdown">
      <div class="dropdown-toggle">
        <div v-if="select_color" style="display: flex;">
          <div class="color_bar" v-for="(e, i) in select_color" :key="i" :style="{ background: e }"></div>
        </div>
        <span v-else>请选择</span>
      </div>
      <ul class="dropdown-menu" :class="{ 'open': isOpen }">
        <li v-for="(group, key, index) in colorGroups" :key="index" @click="handleSelect(group)">
          <span>{{ key }}</span>
          <div style="display: flex;">
            <div class="color_bar" v-for="(e, i) in group" :key="i" :style="{ background: e }"></div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: [
    'color_list', 'select_color', 'API_ROOT', 'ticket', 'from_data'
  ],
  data() {
    return {
      isOpen: false,
      style_value: null,
      selectedColor: null,
      colorOptions: ['Red', 'Green', 'Blue', 'Yellow'],
      style_list: [],

      colorGroups: [],

      /* 数据类型 */
      data_natures: [
        {
          label: '顺序',
          value: 'sequential',
          remark: '1. 顺序方案适合从低到高的有序数据。亮度步骤主导了这些方案的外观，浅色代表低数据值，深色代表高数据值。'
        },
        {
          label: '发散',
          value: 'diverging',
          remark: '2. 发散方案同等重视中间范围的临界值和数据范围两端的极值。图例中间的临界等级或中断用浅色来强调，低和高极端用具有对比色调的深色来强调。'
        },
        {
          label: '定性',
          value: 'qualitative',
          remark: '3. 定性方案并不意味着图例类之间的大小差异，并且色调用于创建类之间的主要视觉差异。定性方案最适合表示名义数据或分类数据。'
        },
      ],

      color_type: null,
      color_type1: [],
    };
  },

  watch: {
    // from_data: {
    //   deep: true,
    //   handler(newData, oldDate) {
    //     console.log('右侧数据监听触发');
    //     this.init(newData.from);
    //   }
    // },

  },

  methods: {
    init() {
      axios({
        headers: {
          ticket: this.ticket
        },
        method: "POST",
        url: this.API_ROOT + this.from_data.api_url,
        data: {},
      }).then((res) => {
        const package_name = res.data.packages[0] || "awtools";
        this.style_value = package_name;
        this.style_list = res.data.packages;
        this.get_group_colors();
      }).catch(error => {

      });
    },
    //获取组颜色
    get_group_colors() {
      axios({
        headers: {
          ticket: this.ticket
        },
        method: "POST",
        url: this.API_ROOT + this.from_data.api_url,
        data: {
          package_name: this.style_value,
          type: this.color_type
        },
      }).then((res) => {
        this.colorGroups = res.data.package_color || [];
      }).catch(error => {

      });
    },
    //分割选择监听
    selectStyle(val) {
      this.style_value = val;
      this.get_group_colors();
    },
    //颜色性质
    colorTypeChange(val) {
      console.log(val);
      this.color_type = val;
      this.get_group_colors();
    },

    toggleDropdown() {
      this.isOpen = !this.isOpen;
    },
    selectColor(color) {
      this.selectedColor = color;
      this.isOpen = false;
    },
    handleOutsideClick(event) {
      if (!this.$refs.dropdown.contains(event.target)) {
        this.isOpen = false;
      }
    },
    handleSelect(item) {
      this.selectedColor = item;
      this.$emit('updataColor', item);
    },
  },
  mounted() {
    this.init();
    document.addEventListener('click', this.handleOutsideClick);
  },
  beforeUnmount() {
    document.removeEventListener('click', this.handleOutsideClick);
  }
};
</script>

