  position: relative;
  display: inline-block;
  width: 100%;
}

.dropdown-toggle {
  cursor: pointer;
  padding: 5px;
  background-color: #eee;
  border: 1px solid #ccc;
}

.dropdown-menu {
  position: absolute;
