  z-index: 1;
  display: none;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #fff;
  border: 1px solid #ccc;
  z-index: 10;
  width: 100%;
  max-height: 150px;
  overflow-y: auto;
}

.dropdown-menu.open {
  display: block;
}

.dropdown-menu li {
  padding: 5px;
  cursor: pointer;
}

.dropdown-menu li:hover {
  background-color: #f1f1f1;
}

