//文件流下载

export function fileStreamDownload(inputs) {
  // 创建 Blob 对象
  const blob = new Blob([inputs], { type: 'application/octet-stream' });

  // 创建下载链接
  const downloadLink = document.createElement('a');
  downloadLink.href = URL.createObjectURL(blob);
  // 设置下载文件名
  downloadLink.download = 'filename.zip';
  // 触发下载
  downloadLink.click();
  // 删除创建的元素
  URL.revokeObjectURL(downloadLink.href);
  downloadLink.remove();

}