import request from '@/utils/request';

//获取左右参数模板
export function get_template_config(data) {
  return request({
    method: 'POST',
    url: `/ms_jk/get_template_config`,
    data
  })
}


//上传文件
export function upload_file(data) {
  return request({
    method: 'POST',
    url: `/ms_jk/upload_file`,
    data
  })
}

//上传文件夹/多个文件
export function upload_files(data) {
  return request({
    method: 'POST',
    url: `/ms_jk/upload_files`,
    data
  })
}


//获取静态图 或者 动态图原始参数
export function get_ms_jk_bubble(data) {
  return request({
    method: 'POST',
    url: `/ms_jk/bubble`,
    data
  })
}



//获取示例数据
export function get_sample_data() {
  return request({
    method: 'POST',
    url: `/ms_jk/sample`
  })
}

//获取原始数据
export function get_raw_data(data) {
  return request({
    method: 'POST',
    url: `/ms_jk/read_file`,
    data
  })
}

//下载文件
export function get_download_file(data) {
  return request({
    method: 'POST',
    url: `/ms_jk/download`,
    responseType: "blob",
    data
  })
}

//参数提交平台
export function parameter_submission_platform(data) {
  return request({
    method: 'POST',
    url: `/ms_jk/submit`,
    data
  })
}