<template>
  <div>
    <div :id="props.data.chart_id" class="chart_box" style="width: 800px;height: 700px;" v-loading="request_loading">
    </div>
  </div>
</template>

<script>
import { ref, onBeforeMount, onMounted, onUpdated, onUnmounted, getCurrentInstance, watch, defineEmits } from 'vue';
import axios from "axios";
export default {
  props: {
    other_value: {
      type: Object
    },
    data: {
      type: Object
    },
    raw_data: {
      type: Object
    },
    API_ROOT: {
      type: String
    },
    ticket: {
      type: String
    }
  },
  components: {
  },
  setup(props, { emit }) {
    const instance = getCurrentInstance();///instance.proxy.$Plotly
    const request_loading = ref(false);
    const chart_data = ref(null);
    const chart_layout = ref(null);
    //接口初始化
    const init = (from = null) => {
      let data = {
        "plot_type": props.data.handle_function,
      };
      if (props.other_value) {
        data = { ...data, ...props.other_value }
      }
      request_loading.value = true;
      axios({
        headers: {
          ticket: props.ticket
        },
        method: "POST",
        url: props.API_ROOT + props.data.api_url,
        data: data,
      }).then((res) => {
        chart_data.value = res.data.data;
        chart_layout.value = res.data.layout;
        // initChart(res.data.data, res.data.layout);
        // instance.proxy.$Plotly.newPlot('charts1', res.data.data, res.data.layout, { displayModeBar: false });
        instance.proxy.$Plotly.newPlot(props.data.chart_id, res.data.data, res.data.layout, { displayModeBar: false });
        request_loading.value = false;
      }).catch((err) => {
        request_loading.value = false;
      });


    };
    //图表初始化
    const initChart = (data, layout) => {
      //props.data.chart_id
      // instance.proxy.$Plotly.newPlot('charts1', data, layout, { displayModeBar: false });
      instance.proxy.$Plotly.newPlot(props.data.chart_id, data, layout, { displayModeBar: false });
    };
    //颜色处理
    const colore_handle = (color) => {
      if (!color) return false;
      let colors = [];
      if (Array.isArray(color)) {
        colors = color;
      } else {
        colors = color.split(",");
      }
      return colors;
    };

    //数据监听更新
    const chart_data_change = (nweProps) => {
      let colors = null;
      const color_ls = colore_handle(nweProps.color_settings.color_list);
      if (color_ls) {
        colors = chart_data.value.map((theme, index) => color_ls[index % color_ls.length]);
        chart_data.value.forEach(element => {
          if (element.hasOwnProperty("marker") && element.marker.hasOwnProperty("color")) {
            element.marker.color = colors
          }
        });
      }

      if (!chart_layout.value) return false;

      /* 图表参数 */
      chart_layout.value.title = {
        text: nweProps.title_config.title || null,
        font: {
          family: nweProps.font_setting.typeface,
          size: nweProps.font_setting.word_size,
          style: nweProps.font_setting.incline ? 'italic' : '', // 设置文字倾斜
          color: nweProps.font_setting.font_color
        }
      };

      /* 坐标轴的字体样式/显示等 */
      if (nweProps.coordinate_axis.xaxis.showtitle) {
        if (nweProps.coordinate_axis.xaxis.title) {
          chart_layout.value.xaxis.title.text = nweProps.coordinate_axis.xaxis.title;
        }
      } else {
        chart_layout.value.xaxis.title.text = "";
      }
      if (nweProps.font_setting.typeface) chart_layout.value.xaxis.title.font.family = nweProps.font_setting.typeface;
      if (nweProps.font_setting.word_size) chart_layout.value.xaxis.title.font.size = nweProps.font_setting.word_size;
      if (nweProps.font_setting.font_color) chart_layout.value.xaxis.title.font.color = nweProps.font_setting.font_color;

      /* y轴数据 */
      if (nweProps.coordinate_axis.yaxis.showtitle) {
        if (nweProps.coordinate_axis.yaxis.title) {
          chart_layout.value.yaxis.title.text = nweProps.coordinate_axis.yaxis.title;
        }
      } else {
        chart_layout.value.yaxis.title.text = "";
      }
      if (nweProps.font_setting.typeface) chart_layout.value.yaxis.title.font.family = nweProps.font_setting.typeface;
      if (nweProps.font_setting.word_size) chart_layout.value.yaxis.title.font.size = nweProps.font_setting.word_size;
      if (nweProps.font_setting.font_color) chart_layout.value.yaxis.title.font.color = nweProps.font_setting.font_color;


      /* 坐标轴显示隐藏 */
      nweProps.coordinate_axis.xaxis.visible ? chart_layout.value.xaxis.visible = true : chart_layout.value.xaxis.visible = false;
      nweProps.coordinate_axis.yaxis.visible ? chart_layout.value.yaxis.visible = true : chart_layout.value.yaxis.visible = false;


      /* 轴线 */
      nweProps.coordinate_axis.xaxis.showline ? chart_layout.value.xaxis.showline = true : chart_layout.value.xaxis.showline = false;
      nweProps.coordinate_axis.yaxis.showline ? chart_layout.value.yaxis.showline = true : chart_layout.value.yaxis.showline = false;


      /* 刻度线显示隐藏 */
      nweProps.coordinate_axis.xaxis.showticklabels ? chart_layout.value.xaxis.showticklabels = true : chart_layout.value.xaxis.showticklabels = false;
      nweProps.coordinate_axis.yaxis.showticklabels ? chart_layout.value.yaxis.showticklabels = true : chart_layout.value.yaxis.showticklabels = false;

      /* 网格线显示隐藏 */
      nweProps.coordinate_axis.xaxis.showgrid ? chart_layout.value.xaxis.showgrid = true : chart_layout.value.xaxis.showgrid = false;
      nweProps.coordinate_axis.yaxis.showgrid ? chart_layout.value.yaxis.showgrid = true : chart_layout.value.yaxis.showgrid = false;


      /* 网格线颜色 */
      if (nweProps.coordinate_axis.grid_color) chart_layout.value.xaxis.gridcolor = nweProps.coordinate_axis.grid_color;
      if (nweProps.coordinate_axis.grid_color) chart_layout.value.yaxis.gridcolor = nweProps.coordinate_axis.grid_color;

      /* 图表设置 */
      chart_layout.value.width = nweProps.chart_setting.width;
      chart_layout.value.height = nweProps.chart_setting.height;
      chart_layout.value.margin = {
        t: nweProps.chart_setting.t,
        b: nweProps.chart_setting.b,
        l: nweProps.chart_setting.l,
        r: nweProps.chart_setting.r
      };
      /* 图例 */
      chart_layout.value.legend.visible = nweProps.legend.show;
      chart_layout.value.legend.x = nweProps.legend.x;
      chart_layout.value.legend.y = nweProps.legend.y;

      /*  */



      setTimeout(() => {
        // initChart(chart_data.value, chart_layout.value);
        instance.proxy.$Plotly.newPlot(props.data.chart_id, chart_data.value, chart_layout.value, { displayModeBar: false });
      }, 300);
    };

    //监听other_value
    watch(() => props.other_value, (newValue, oldValue) => {
      init(newValue);
    });
    //监听 动态参数
    watch(() => props.raw_data, (newValue, oldValue) => {
      console.log('动态参数变化');
      chart_data_change(newValue);
    });

    onBeforeMount(() => {

    });
    onMounted(() => {
      if (props.other_value) init();
      // const targetElement = document.getElementById('charts1');
      const targetElement = document.getElementById(props.data.chart_id);
      function handleResize() {
        const width = targetElement.offsetWidth, height = targetElement.offsetHeight;
        if (width && height && chart_layout.value) {
          chart_layout.value.width = width - 50;
          chart_layout.value.height = height - 50;
          initChart(chart_data.value, chart_layout.value);
        }
      }
      const resizeObserver = new ResizeObserver(handleResize);
      resizeObserver.observe(targetElement);
      const mutationObserver = new MutationObserver(() => {
        // 当元素的子节点发生变化时，重新观察目标元素
        resizeObserver.disconnect();
        resizeObserver.observe(targetElement);
      });
      mutationObserver.observe(targetElement.parentNode, { childList: true });

    });
    onUpdated(() => {

    });
    onUnmounted(() => {

    });
    return {
      props, chart_data, chart_layout, request_loading
    };
  }
}
</script>
<style scoped>
.from_unit {
  margin-bottom: 30px;
  text-align: right;
}

