import Cookies from 'js-cookie';

const ticket = 'ticket';


//ticket
export function getTicket() {
  return Cookies.get(ticket);
}
export function setTicket(t) {
  return Cookies.set(ticket, t)
}
export function removeTicket() {
  return Cookies.remove(ticket)
}
