<template>
  <div>选择 </div>
  <div style="min-height: 400px;" v-loading="is_loading">
    <el-image v-if="src" :src="src"></el-image>
    <el-empty v-else description="暂无数据" />
  </div>
</template>

<script >

import { ref, watch, onBeforeUnmount } from 'vue'
import axios from "axios";
export default {
  props: ["other_value", "from_data", 'API_ROOT', 'ticket'],
  data() {
    return {
      // src: 'https://t7.baidu.com/it/u=839828294,1619278046&fm=193&f=GIF',
      src: null,
      headers: {
        "ticket": "",
      },

      is_loading: false,


      copy_other_value: null

    };
  },
  created() {
    this.headers['ticket'] = this.ticket
  },
  mounted() {
    if (this.other_value) {
      this.init(this.other_value);
      this.copy_other_value = this.other_value;
    }

  },
  watch: {
    from_data: {
      deep: true,
      handler(newData, oldDate) {
        console.log('右侧数据监听触发');
        console.log(newData);
        this.init(newData.from);
      }
    },
    other_value: {
      deep: true,
      handler(newData, oldDate) {
        this.init(newData);
      }
    }
  },
  methods: {
    init(from = null) {
      console.log('数据没有？？');
      console.log(this.other_value);
      let data = {
        "plot_type": 1,
      };
      if (from) {
        data = { ...this.other_value, ...data, ...from, }
      }
      console.log(data);
      this.headers = {
        ticket: this.ticket
      };
      this.is_loading = true;
      axios({
        headers: this.headers,
        method: "POST",
        url: this.API_ROOT + this.from_data.api_url,
        data: data,
      }).then((res) => {
        //image_task_id

        console.log('图片接口数据');
        console.log(res);

        if (res.data.image_task_id) {
          localStorage.setItem('image_task_id', res.data.image_task_id || null);
        }

        if (!this.from_data.data_path) {
          this.src = 'data:image/png;base64,' + res.data.base64list[0];
        } else if (res.data[this.from_data.data_path]) {
          this.src = 'data:image/png;base64,' + res.data[this.from_data.data_path][0]
        }
        this.is_loading = false;
      }).catch(error => {
        this.is_loading = false;
      });
    }
  }
}
</script>

